<template>
  <div class="page5">
    <all_titel :indexText="indexText"></all_titel>
    <div class="contact">
      <div class="contactType">
        <div class="type-item">
          <div class="type-img">
            <img src="../../../static/icon/icon-adree.png" alt="">
          </div>
          <div class="item-text">
            <div class="item-title">详细地址</div>
            <div class="text-center">河南省平顶山市贸易广场</div>
          </div>
        </div>
        <div class="type-item">
          <div class="type-img">
            <img src="../../../static/icon/icon-ipone.png" alt="">
          </div>
          <div class="item-text">
            <div class="item-title">联系电话</div>
            <div class="text-center">0375-7088900</div>
            <div class="text-center">15537557010</div>
          </div>
        </div>
        <div class="type-item">
          <div class="type-img">
            <img src="../../../static/icon/icon-watch.png" alt="">
          </div>
          <div class="item-text">
            <div class="item-title">微信</div>
            <div class="text-center">
              <img src="../../../static/icon/weChat.png" alt="">
            </div>
          </div>
        </div>
        <div class="type-item">
          <div class="type-img">
            <img src="../../../static/icon/icon-qq.png" alt="">
          </div>
          <div class="item-text">
            <div class="item-title">QQ</div>
            <div class="text-center">
              <img src="../../../static/icon/qq.png" alt="">
            </div>
          </div>
        </div>
      </div>
      <div class="mageLeft">
        <baidu_map :m="giveData"></baidu_map>
      </div>
    </div>
  </div>
</template>

<script>
  import all_titel from '@/components/pageNavigation/pageNavigation'
  import baidu_map from '@/components/baidu_map/baidu_map'
  export default {
    name: "page5",
    components:{
      all_titel,
      baidu_map
    },
    data () {
      return {
        giveData:{  //填写金纬度
          longitude:113.372268,
          latitude:33.737724
        },
      }
    },
    computed:{
      indexText(){
        return this.$store.state.indexText;
      }
    },
  }
</script>

<style scoped>
  .page5{
    width: 960px;
    overflow: hidden;
    margin: 10px auto;
  }
  .contact{
    width: 100%;
    margin: 30px auto 50px auto;
  }
  .contactType{
    width: 100%;
    overflow: hidden;
    margin-bottom: 25px;
  }
  .type-item{
    float: left;
    background: #ececec;
    width: 230px;
    height: 223px;
    margin-right: 12px;
  }
  .type-item:nth-child(4n){
    margin-right: 0;
  }
  .type-img{
    width: 100%;
    margin-top: 25px;
    text-align: center;
  }
  .type-img img{
    width: 60px;
    height: 60px;
    display: inline-block;
  }
.item-text{
  margin-top: 10px;
  color: #666;
  font-size: 14px;
  text-align: center;
}
.item-title{
  margin-bottom: 5px;
}
.mageLeft{
  width: 100%;
  height: 300px;
  overflow: hidden;
  border-right: 1px solid #ccc;
}
.text-center img{
  width: 90px;
  height: 90px;
  display: block;
}
/*移动端*/
  @media screen and (max-width: 768px) {
    .page5{
      width: 94%;
      margin: 10px auto;
      overflow: hidden;
    }
    .contact{
      width: 100%;
      margin: 20px auto 15px auto;
    }
    .contactType{
      width: 100%;
      overflow: hidden;
      margin-bottom: 10px;
    }
    .type-item{
      float: left;
      background: #ececec;
      width: 49%;
      height: 180px;
      margin-right: 2%;
      margin-bottom: 10px;
    }
    .type-item:nth-child(2n){
      margin-right: 0;
    }
    .type-img{
      width: 100%;
      margin-top: 15px;
      text-align: center;
    }
    .type-img img{
      width: 40px;
      height: 40px;
      display: inline-block;
    }
    .item-text{
      margin-top: 5px;
      color: #666;
      font-size: 12px;
      text-align: center;
    }
    .mageLeft{
      width: 100%;
      height: 200px;
      overflow: hidden;
      border-right: 1px solid #ccc;
    }
  }
</style>



